<template>
    <div>
        <!-- 面包屑导航区域 -->
        <el-breadcrumb :separator-icon="ArrowRight">
            <el-breadcrumb-item :to="{ path: '/home/welcome' }">首页</el-breadcrumb-item>
            <el-breadcrumb-item>权限管理</el-breadcrumb-item>
            <el-breadcrumb-item>角色列表</el-breadcrumb-item>
        </el-breadcrumb>
        <!-- 卡片视图区域 -->
        <el-card>
            <!-- 添加角色按钮区域 -->
            <el-row>
                <el-col>
                    <el-button type="primary" size="large" @click="showAddDialog()">添加角色</el-button>
                </el-col>
            </el-row>
            <!-- 角色列表区域 -->
            <el-table :data="rolesList" border stripe row-key="id">
                <!-- 展开列 -->
                <el-table-column type="expand">
                    <template #default="scope">
                        <el-row :class="['vcenter', 'bdbottom', index1 === 0 ? 'bdtop' : '']"
                            v-for="(firstLevel, index1) in scope.row.children" :key="firstLevel.id" class="role-expand">
                            <!-- 一级权限渲染 -->
                            <el-col :span="5">
                                <el-tag closable type="danger"
                                    @close="removeRightById(scope.row, firstLevel.id, firstLevel.authName)">{{
                                        firstLevel.authName }}</el-tag>
                                <el-icon>
                                    <CaretRight />
                                </el-icon>
                            </el-col>
                            <!-- 二级权限 和 三级权限 渲染 -->
                            <el-col :span="19">
                                <el-row :class="['vcenter', index2 === 0 ? '' : 'bdtop']"
                                    v-for="(secondLevel, index2) in firstLevel.children" :key="secondLevel.id">
                                    <!-- 二级级权限渲染 -->
                                    <el-col :span="6">
                                        <el-tag closable type="warning"
                                            @close="removeRightById(scope.row, secondLevel.id, secondLevel.authName)">{{
                                                secondLevel.authName }}</el-tag>
                                        <el-icon>
                                            <CaretRight />
                                        </el-icon>
                                    </el-col>
                                    <!-- 三级权限渲染 -->
                                    <el-col :span="18">
                                        <el-tag closable type="success" v-for="thirdLevel in secondLevel.children"
                                            :key="thirdLevel.id"
                                            @close="removeRightById(scope.row, thirdLevel.id, thirdLevel.authName)">{{
                                                thirdLevel.authName }}</el-tag>
                                    </el-col>
                                </el-row>
                            </el-col>
                        </el-row>
                        <!-- <pre>{{ scope.row }}</pre> -->
                    </template>
                </el-table-column>
                <!-- 索引列 -->
                <el-table-column type="index" label="#"></el-table-column>
                <el-table-column label="角色名称" prop="roleName"></el-table-column>
                <el-table-column label="角色描述" prop="roleDesc"></el-table-column>
                <el-table-column label="操作" width="234px">
                    <template #default="scope">
                        <el-button type="primary" :icon="Edit" size="small" style="padding: 5px 6px"
                            @click="showEditDialog(scope.row.id)">编辑</el-button>
                        <el-button type="danger" :icon="Delete" size="small" style="padding: 5px 6px"
                            @click="removeRoleById(scope.row.id)">删除</el-button>
                        <el-button type="warning" :icon="Setting" size="small" style="padding: 5px 6px"
                            @click="showSetRightsDialog(scope.row)">分配权限</el-button>
                    </template>
                </el-table-column>
            </el-table>
        </el-card>
        <!-- 添加角色的对话框区域 -->
        <el-dialog v-model="addDialogVisible" title="添加角色" width="50%" @close="addDialogClosed">
            <!-- 内容主体区域 -->
            <el-form :model="addRoleForm" label-width="80px" :rules="addRoleRules" ref="addFormRef">
                <el-form-item label="角色名称" prop="roleName">
                    <el-input v-model="addRoleForm.roleName" />
                </el-form-item>
                <el-form-item label="角色描述" prop="roleDesc">
                    <el-input v-model="addRoleForm.roleDesc" />
                </el-form-item>
            </el-form>
            <!-- 底部区域 -->
            <template #footer>
                <span class="dialog-footer">
                    <el-button @click="addDialogVisible = false">取消</el-button>
                    <el-button type="primary" @click="addRole">
                        确认
                    </el-button>
                </span>
            </template>
        </el-dialog>
        <!-- 编辑角色的对话框区域 -->
        <el-dialog v-model="editDialogVisible" title="修改角色信息" width="50%" @close="editDialogClosed">
            <!-- 内容主体区域 -->
            <el-form :model="editRoleForm" label-width="80px" :rules="editRoleRules" ref="editFormRef">
                <el-form-item label="角色名称">
                    <el-input v-model="editRoleForm.roleName" disabled />
                </el-form-item>
                <el-form-item label="角色描述" prop="roleDesc">
                    <el-input v-model="editRoleForm.roleDesc" />
                </el-form-item>
            </el-form>
            <!-- 底部区域 -->
            <template #footer>
                <span class="dialog-footer">
                    <el-button @click="editDialogVisible = false">取消</el-button>
                    <el-button type="primary" @click="editRole">
                        确认
                    </el-button>
                </span>
            </template>
        </el-dialog>
        <!-- 分配权限的对话框 -->
        <el-dialog v-model="setRightsDialogVisible" title="分配权限" width="50%" @close="setRightsDialogClosed">
            <!-- 权限列表的树形控件 -->
            <el-tree :data="rightsList" :props="treeProps" show-checkbox node-key="id" default-expand-all :default-checked-keys="defKeys" ref="treeRef"/>
            <template #footer>
                <span class="dialog-footer">
                    <el-button @click="setRightsDialogVisible = false">取消</el-button>
                    <el-button type="primary" @click="allotRights">确认</el-button>
                </span>
            </template>
        </el-dialog>
    </div>
</template>

<script>
import { ArrowRight, Edit, Delete, Setting } from '@element-plus/icons-vue'
export default {
    data() {
        return {
            ArrowRight, Edit, Delete, Setting,
            // 所有角色列表数据
            rolesList: [],
            // 添加角色对话框的显示与隐藏
            addDialogVisible: false,
            // 添加角色的信息
            addRoleForm: {},
            // 编辑角色的信息校验
            addRoleRules: {
                roleName: [
                    { required: true, message: '必须输入角色名称', trigger: 'blur' },
                    { min: 2, message: '角色名称至少需要两个汉字', trigger: 'blur' }
                ],
                roleDesc: [
                    { required: true, message: '必须输入角色描述', trigger: 'blur' },
                    { min: 2, message: '角色描述至少需要两个汉字', trigger: 'blur' }
                ]
            },
            // 编辑角色对话框的显示与隐藏
            editDialogVisible: false,
            // 编辑角色的信息校验
            editRoleRules: {
                roleDesc: [
                    { required: true, message: '必须输入角色没描述', trigger: 'blur' },
                    {
                        min: 2, message: '角色描述至少需要两个汉字', trigger: 'blur'
                    }
                ]
            },
            // 编辑角色的信息
            editRoleForm: {},
            // 编辑角色信息的备份
            editRoleBackup: {},
            // 控制分配权限对话框的显示与隐藏
            setRightsDialogVisible: false,
            // 所有权限的数据
            rightsList: [],
            // 树形控件的属性绑定对象
            treeProps: {
                label: 'authName',
                children: 'children'
            },
            // 权限树中默认选中的节点id值数组
            defKeys: [],
            // 当前即将分配权限的角色id
            setRightsRoleId: ''
        }
    },
    created() {
        this.getRolesList();
    },
    methods: {
        // 获取角色列表
        getRolesList() {
            this.$api.httpGet('roles', {
                showOuter: 'error',
                error: ['获取角色列表失败！']
            }).then(res => {
                this.rolesList = res.data;
            })
        },
        // 显示添加角色的对话框
        showAddDialog() {
            this.addDialogVisible = true;
        },
        // 监听添加角色对话框的关闭事件
        addDialogClosed() {
            this.$refs.addFormRef.resetFields();
        },
        // 添加角色
        addRole() {
            this.$api.httpPost('roles', this.addRoleForm, {showOuter: 'error', success: ['角色添加成功！'], error: ['角色添加失败！']}, 201)
            .then(() => {
                this.addDialogVisible = false;
                this.getRolesList();
            })
        },
        // 删除角色的指定权限
        removeRightById(role, rightId, authName) {
            // 弹出框询问用户是否删除权限
            this.$confirm(`此操作将永久删除该角色的【${authName}】权限，是否继续？`, '权限删除', {
                confirmButtonText: '确认',
                cancelButtonText: '取消',
                type: 'warning',
            }).then(() => {
                // 用户点击确认，删除权限
                this.$api.httpDelete(`roles/${role.id}/rights/${rightId}`, { showOuter: 'error', error: ['删除权限失败！'] })
                    .then(res => {
                        // 删除成功，刷新该角色的权限信息
                        role.children = res.data;
                    })
            }).catch(() => {
                // 用户点击取消，弹出取消删除提示框
                this.$message({
                    type: 'info',
                    message: '取消删除',
                    duration: 1200
                })
            });
        },
        // 显示编辑角色的对话框
        async showEditDialog(id) {
            // 根据id获取角色信息
            const res = await this.$api.httpGet('roles/' + id, {
                showOuter: 'error',
                error: ['获取角色信息失败！']
            });
            // 获取角色信息成功，显示编辑对话框
            this.editRoleForm = res.data;
            this.editRoleBackup = { ...res.data };
            this.editDialogVisible = true;
        },
        // 监听修改角色对话框的关闭事件
        editDialogClosed() {
            this.$refs.editFormRef.resetFields();
        },
        // 点击确认按钮，编辑角色
        async editRole() {
            // 检查角色信息是否发生修改
            if (this.editRoleForm.roleDesc === this.editRoleBackup.roleDesc) return this.$message({ message: '角色信息未修改，更新取消', duration: 1200 });

            // 发送put网络请求，编辑角色
            const res = await this.$api.httpPut('roles/' + this.editRoleForm.roleId, this.editRoleForm, { success: ['更新角色信息成功！'], error: ['更新角色信息失败！'], showOuter: 'error' });

            // 编辑成功，关闭提示框
            this.editDialogVisible = false;
            // 刷新角色列表
            this.getRolesList();
        },
        // 通过id删除角色
        removeRoleById(id) {
            // 弹出框询问用户是否删除数据
            this.$confirm('此操作将永久删除该角色，是否继续？', '删除', {
                confirmButtonText: '确认',
                cancelButtonText: '取消',
                type: 'warning',
            }).then(() => {
                // 用户点击确认，删除角色
                this.$api.httpDelete('roles/' + id, { showOuter: 'error', success: ['删除角色成功！'], error: ['删除角色失败！'] })
                    .then(() => {
                        // 删除成功，刷新角色列表
                        this.getRolesList();
                    })
            }).catch(() => {
                // 用户点击取消，弹出取消删除提示框
                this.$message({
                    type: 'info',
                    message: '取消删除',
                    duration: 1200
                })
            });
        },
        // 展示分配权限的对话框
        async showSetRightsDialog(role) {
            // 保存分配权限的角色id，供后面发送修改权限的请求使用
            this.setRightsRoleId = role.id;
            // 获取所有的权限列表
            const res = await this.$api.httpGet('rights/tree', { showOuter: 'error', error: ['获取权限数据失败！'] })
            // 保存获取到的权限列表
            this.rightsList = res.data;
            
            // 递归获取三级节点的id
            this.getLeafKeys(role, this.defKeys);

            this.setRightsDialogVisible = true;
        },
        // 通过递归的形式，获取角色下所有三级权限的id，并保存到 defKeys 数组中
        getLeafKeys(node, arr) {
            // 所有的一，二级节点都有children属性，至少为一个空数组。
            //  如果当前node节点没有children属性，则表示为三级节点。
            if (!node.children) {
                return arr.push(node.id);
            }
            
            node.children.forEach(element => this.getLeafKeys(element, arr));
        },
        // 监听分配权限对话框的关闭事件
        setRightsDialogClosed() {
            // 清空权限树中默认选中的节点id值数组中的数据
            this.defKeys = [];
        },
        // 点击确认按钮为角色分配权限
        allotRights() {
            const keys = [ 
                ...this.$refs.treeRef.getCheckedKeys(),
                ...this.$refs.treeRef.getHalfCheckedKeys()
            ]
            
            const idStr = keys.join(',');

            this.$api.httpPost(`roles/${this.setRightsRoleId}/rights`, {rids: idStr}, {showOuter: 'error', success: ['分配权限成功！'], error: ['分配权限失败！']})
            .then(res => {
                // 刷新角色列表
                this.getRolesList();
                // 隐藏对话框
                this.setRightsDialogVisible = false;
            })
        }
    }
}
</script>

<style lang="less" scoped>
.role-expand {
    .el-tag {
        margin: 7px;
    }
}

.bdtop {
    border-top: 1px solid #eee;
}

.bdbottom {
    border-bottom: 1px solid #eee;
}

.vcenter {
    display: flex;
    align-items: center;
}
</style>