<template>
    <div>
        <!-- 面包屑导航区域 -->
        <el-breadcrumb :separator-icon="ArrowRight">
            <el-breadcrumb-item :to="{ path: '/home/welcome' }">首页</el-breadcrumb-item>
            <el-breadcrumb-item>用户管理</el-breadcrumb-item>
            <el-breadcrumb-item>用户列表</el-breadcrumb-item>
        </el-breadcrumb>

        <!-- 卡片视图区域 -->
        <el-card>
            <div class="mt-4">
                <el-row :gutter="20">
                    <el-col :span="8">
                        <!-- 搜索与添加用户区域 -->
                        <el-input v-model="queryInfo.query" placeholder="请输入用户名" class="input-with-select" clearable
                            @clear="getUserList">
                            <template #append>
                                <el-button :icon="Search" @click="getUserList" />
                            </template>
                        </el-input>
                    </el-col>
                    <el-col :span="4">
                        <el-button type="primary" @click="addDialogVisible = true">添加用户</el-button>
                    </el-col>
                </el-row>
            </div>
            <!-- 用户列表显示区域 -->
            <el-table :data="userList" border stripe>
                <el-table-column label="#" type="index" width="35"></el-table-column>
                <el-table-column label="姓名" prop="username" width="100"></el-table-column>
                <el-table-column label="邮箱" prop="email" width="140"></el-table-column>
                <el-table-column label="电话" prop="mobile" width="120"></el-table-column>
                <el-table-column label="角色" prop="role_name" width="90"></el-table-column>
                <el-table-column label="状态" width="65">
                    <template #default="scope">
                        <el-switch v-model="scope.row.mg_state" @change="userStateChanged(scope.row)"></el-switch>
                    </template>
                </el-table-column>
                <el-table-column label="操作" min-width="160px">
                    <template #default="scope">
                        <!-- 修改按钮 -->
                        <el-tooltip content="编辑" placement="top" :enterable="false">
                            <el-button type="primary" :icon="Edit" size="small" @click="showEditDialog(scope.row.id)" />
                        </el-tooltip>
                        <!-- 删除按钮 -->
                        <el-tooltip content="删除" placement="top" :enterable="false">
                            <el-button type="danger" :icon="Delete" size="small" @click="removeUserById(scope.row.id)" />
                        </el-tooltip>
                        <!-- 分配角色按钮 -->
                        <el-tooltip content="分配角色" placement="top" :enterable="false">
                            <el-button type="warning" :icon="Setting" size="small" @click="showSetRoleDialog(scope.row)" />
                        </el-tooltip>
                    </template>
                </el-table-column>
            </el-table>
            <!-- 分页区域 -->
            <el-pagination v-model:current-page="queryInfo.pagenum" v-model:page-size="queryInfo.pagesize"
                :page-sizes="[1, 2, 5, 10]" small background layout="total, sizes, prev, pager, next, jumper" :total="total"
                @size-change="handleSizeChange" @current-change="handleCurrentChange" />
        </el-card>
        <!-- 添加用户的对话框区域 -->
        <el-dialog v-model="addDialogVisible" title="添加用户" width="50%" @close="addDialogClosed">
            <!-- 内容主体区域 -->
            <el-form :model="addUserForm" label-width="120px" :rules="addUserRules" ref="addFormRef">
                <el-form-item label="用户名称" prop="username">
                    <el-input v-model="addUserForm.username" />
                </el-form-item>
                <el-form-item label="用户密码" prop="password">
                    <el-input show-password v-model="addUserForm.password" />
                </el-form-item>
                <el-form-item label="再次输入密码" prop="checkPassword">
                    <el-input show-password v-model="addUserForm.checkPassword" />
                </el-form-item>
                <el-form-item label="邮箱" prop="email">
                    <el-input v-model="addUserForm.email" />
                </el-form-item>
                <el-form-item label="电话号码" prop="mobile">
                    <el-input v-model="addUserForm.mobile" />
                </el-form-item>
            </el-form>
            <!-- 底部区域 -->
            <template #footer>
                <span class="dialog-footer">
                    <el-button @click="addDialogVisible = false">取消</el-button>
                    <el-button type="primary" @click="addUser">
                        确认
                    </el-button>
                </span>
            </template>
        </el-dialog>
        <!-- 编辑用户的对话框区域 -->
        <el-dialog v-model="editDialogVisible" title="修改用户信息" width="50%" @close="editDialogClosed">
            <!-- 内容主体区域 -->
            <el-form :model="editUserForm" label-width="120px" :rules="editUserRules" ref="editFormRef">
                <el-form-item label="用户名称">
                    <el-input v-model="editUserForm.username" disabled />
                </el-form-item>
                <el-form-item label="邮箱" prop="email">
                    <el-input v-model="editUserForm.email" />
                </el-form-item>
                <el-form-item label="电话号码" prop="mobile">
                    <el-input v-model="editUserForm.mobile" />
                </el-form-item>
            </el-form>
            <!-- 底部区域 -->
            <template #footer>
                <span class="dialog-footer">
                    <el-button @click="editDialogVisible = false">取消</el-button>
                    <el-button type="primary" @click="editUser">
                        确认
                    </el-button>
                </span>
            </template>
        </el-dialog>
        <!-- 分配角色的对话框区域 -->
        <el-dialog v-model="setRoleDialogVisible" title="分配角色" width="50%" @close="setRoleDialogClosed">
            <!-- 内容主体区域 -->
            <div>
                <p>当前用户：{{ userInfo.username }}</p>
                <p>用户角色：{{ userInfo.role_name }}</p>
                <p>分配新角色：
                    <el-select v-model="selectedRoleId" placeholder="选择角色">
                        <el-option v-for="role in rolesList" :key="role.id" :label="role.roleName" :value="role.id" />
                    </el-select>
                </p>
            </div>
            <!-- 底部区域 -->
            <template #footer>
                <span class="dialog-footer">
                    <el-button @click="setRoleDialogVisible = false">取消</el-button>
                    <el-button type="primary" @click="setRole">
                        确认
                    </el-button>
                </span>
            </template>
        </el-dialog>
    </div>
</template>
    
<script>
import { ArrowRight, Search, Edit, Delete, Setting } from '@element-plus/icons-vue'
export default {
    data() {
        // 电话号码校验
        const validatePhoneNumber = (rule, value, callback) => {
            const reg = /^(0|86|17951)?(13[0-9]|15[012356789]|17[678]|18[0-9]|14[57])[0-9]{8}$/;
            if (!reg.test(value) && value != '') {
                callback(new Error('请输入正确的电话号码'));
            } else {
                callback();
            }
        };
        // 邮箱校验
        const validateEmail = (rule, value, callback) => {
            var emailRegExp = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(\.[a-zA-Z0-9_-])+/;
            var emailRegExp1 = /^([a-zA-Z]|[0-9])(\w|\-)+@[a-zA-Z0-9]+\.([a-zA-Z]{2,4})$/;
            if ((!emailRegExp.test(value) && value != '') || (!emailRegExp1.test(value) && value != '')) {
                callback(new Error('请输入有效邮箱格式！'));
            } else {
                callback();
            }
        };
        // 密码校验
        const validatePass = (rule, value, callback) => {
            // 如果两次输入的密码都不为空，则触发二次密码的验证rule
            if (value !== '' && this.addUserForm.checkPassword !== '')
                return this.$refs.addFormRef.validateField('checkPassword', () => null)
            callback()
        }
        // 密码二次验证
        const validatePass2 = (rule, value, callback) => {
            // 两次输入的密码都不为空的情况下判断两个密码是否一致
            if (value !== '' && value !== this.addUserForm.password)
                return callback(new Error("两次输入的密码不一致"))
            callback()
        }
        return {
            ArrowRight, Search, Edit, Delete, Setting,
            // 用户列表的请求参数
            queryInfo: {
                query: '',
                pagenum: 1,
                pagesize: 2
            },
            // 总数据条数
            total: 0,
            // 用户列表
            userList: [],
            // 控制添加用户对话框的显示与隐藏
            addDialogVisible: false,
            // 添加用户的信息
            addUserForm: {
                username: "bcd123456",
                password: "bcd123456",
                checkPassword: "bcd123456",
                email: "bcd@123456.com",
                mobile: "13612345678"
            },
            // 添加用户框的输入信息验证
            addUserRules: {
                // 用户名验证
                username: [
                    { required: true, message: '请输入登录名称', trigger: 'blur' },
                    { min: 3, max: 10, message: '用户名长度 3 到 10 个字符', trigger: 'blur' },
                ],
                // 密码验证
                password: [
                    { required: true, message: '请输入登录密码', trigger: 'blur' },
                    { min: 6, max: 20, message: '密码长度 6 到 20 个字符', trigger: 'blur' },
                    { validator: validatePass, trigger: 'blur' },
                ],
                // 二次输入密码的验证
                checkPassword: [
                    { required: true, message: '请再次输入登录密码', trigger: 'blur' },
                    { min: 6, max: 20, message: '密码长度 6 到 20 个字符', trigger: 'blur' },
                    { validator: validatePass2, trigger: 'blur' },
                ],
                // 邮箱验证
                email: [
                    { required: true, message: '请输入邮箱', trigger: 'blur' },
                    { validator: validateEmail, trigger: 'blur' },
                ],
                // 电话号码验证
                mobile: [
                    { required: true, message: '请输入电话号码', trigger: 'blur' },
                    { validator: validatePhoneNumber, trigger: 'blur' },
                ],
            },
            // 控制编辑用户对话框的显示与隐藏
            editDialogVisible: false,
            // 编辑用户的信息
            editUserForm: {},
            //编辑用户框的输入信息验证
            editUserRules: {
                // 邮箱验证
                email: [
                    { required: true, message: '请输入邮箱', trigger: 'blur' },
                    { validator: validateEmail, trigger: 'blur' },
                ],
                // 电话号码验证
                mobile: [
                    { required: true, message: '请输入电话号码', trigger: 'blur' },
                    { validator: validatePhoneNumber, trigger: 'blur' },
                ]
            },
            // 编辑用户的保留信息
            editUserFormBackup: {},
            // 需要被分配角色的用户信息
            userInfo: {},
            // 控制分配角色对话框的显示与隐藏
            setRoleDialogVisible: false,
            // 所有的角色列表
            rolesList: [],
            // 分配角色树形控件的属性
            setRoleTreeProps: {
                label: 'roleName',
                children: 'children'
            },
            // 已选中的角色id值
            selectedRoleId: '',
        }
    },
    created() {
        // 在页面渲染前发起网路请求，获取页面的数据
        this.getUserList();
    },
    methods: {
        async getUserList() {
            const { data: res } = await this.$http.get('users', { params: this.queryInfo });
            if (res.meta.status !== 200) return this.$message.error({ message: '获取用户名列表失败', duration: 1200 });
            this.total = res.data.total;
            this.userList = res.data.users;
        },
        // 监听 pageSize 改变的事件
        handleSizeChange(newSize) {
            this.queryInfo.pagesize = newSize;
            this.getUserList();
        },
        // 监听 页码值 改变的事件
        handleCurrentChange(newPageCount) {
            this.queryInfo.pagenum = newPageCount;
            this.getUserList();
        },
        // 监听用户状态改变
        async userStateChanged(userInfo) {
            const { data: res } = await this.$http.put(`users/${userInfo.id}/state/${userInfo.mg_state}`);
            if (res.meta.status !== 200) {
                this.$message.error({ message: '更新用户状态失败！', duration: 1200 });
                userInfo.mg_state = !userInfo.mg_state;
            }
            this.$message.success({ message: '更新用户状态成功！', duration: 1200 });
        },
        // 监听添加用户对话框的关闭事件
        addDialogClosed() {
            this.$refs.addFormRef.resetFields();
        },
        // 点击确认按钮添加用户
        addUser() {
            this.$refs.addFormRef.validate(async valid => {
                if (!valid) return
                // 验证通过，发起添加用户的网络请求
                const { data: res } = await this.$http.post('users', this.addUserForm);
                if (res.meta.status !== 201) {
                    return this.$message.error({ message: '添加用户失败！' + res.meta.msg, duration: 1200 });
                }

                this.$message.success({ message: '添加用户成功！', duration: 1200 });
                // 隐藏添加用户的对话框
                this.addDialogVisible = false;
                // 刷新用户列表
                this.getUserList();
            });
        },
        // 展示修改用户信息对话框
        showEditDialog(id) {
            // 发起请求，获取最新的数据
            this.$api.httpGet(`users/${id}`, { error: ['查询用户信息失败！'], showOuter: 'error' })
                .then(res => {
                    // 将查询到的用户信息对象保存起来
                    this.editUserForm = res.data;
                    this.editUserFormBackup = { ...res.data };
                    this.editDialogVisible = true;
                });
        },
        // 监听修改用户对话框的关闭事件
        editDialogClosed() {
            this.$refs.editFormRef.resetFields();
        },
        //  点击确认按钮编辑用户
        editUser() {
            this.$refs.editFormRef.validate(valid => {
                if (!valid) return
                // 验证通过，查看数据是否被修改
                if (this.editUserForm.email === this.editUserFormBackup.email && this.editUserForm.mobile === this.editUserFormBackup.mobile) return this.$message({ message: '用户信息未改动', duration: 1200 });
                // 用户信息改动，发起修改用户的网络请求
                this.$api.httpPut('users/' + this.editUserForm.id, this.editUserForm, { error: ['更新用户信息失败！'], showOuter: 'error' })
                    .then(res => {
                        // 关闭对话框
                        this.editDialogVisible = false;
                        // 刷新用户列表
                        this.getUserList();
                        // 提示修改成功
                        this.$message.success({ message: '更新用户信息成功', duration: 1200 });
                    })
            });
        },
        // 通过id删除用户
        removeUserById(id) {
            // 弹出框询问用户是否删除数据
            this.$confirm('此操作将永久删除该用户，是否继续？', '删除', {
                confirmButtonText: '确认',
                cancelButtonText: '取消',
                type: 'warning',
            }).then(() => {
                // 用户点击确认，删除用户
                this.$api.httpDelete('users/' + id, { showOuter: 'error', success: ['删除用户成功！'], error: ['删除用户失败！'] })
                    .then(() => {
                        // 删除成功，刷新用户列表
                        this.getUserList();
                    })
            }).catch(() => {
                // 用户点击取消，弹出取消删除提示框
                this.$message({
                    type: 'info',
                    message: '取消删除',
                    duration: 1200
                })
            });
        },
        // 显示分配角色的对话框
        async showSetRoleDialog(userInfo) {
            this.userInfo = userInfo;

            // 展示对话框之前，获取所有的角色列表 和 用户的所有角色
            const { data: rolesList } = await this.$api.httpGet('roles', { showOuter: 'error', error: ['角色列表获取失败！'] });
            this.rolesList = rolesList;
            this.setRoleDialogVisible = true;
        },
        // 点击确认按钮，为用户分配角色
        setRole() {
            // 判断用户是否选择其中一个角色
            if (!this.selectedRoleId) return this.$message({message: '您没有为用户指定角色', duration: 1200});

            // 判断用户新选择的角色是否和原来的是一样的
            // 想要正确判断，必须在用户列表的用户对象中包含角色id这个属性，无法通过角色名直接判定

            // 发送put请求，分配用户角色
            this.$api.httpPut(`users/${this.userInfo.id}/role`, {rid: this.selectedRoleId}, {error: ['分配用户角色失败']})
            .then(() => {
                this.setRoleDialogVisible = false;
                this.getUserList();
            })
        },
        // 监听分配角色对话框的关闭事件
        setRoleDialogClosed() {
            // 将上次选中的角色id清除
            this.selectedRoleId = '';
            // 将上次获取的用户信息清除
            this.userInfo = {};
        }
    }
}
</script>
    
<style></style>